                    self.$localStorage.set('htmlLiveVid', renderFullVid);
                    self.$localStorage.set('idLiveVid', itemsVideoClient[0].id);
                    self.$localStorage.set('accessToken', accessToken);

                    self.$router.push("/app/openLive/" + itemsVideoClient[0].id);
